<template>
  <section class="app-in">
    <Header></Header>
    <div class="main">
      <div class="main-holdQuery">
        <div class="search-header">
          <div class="title">持股查询</div>
          <div style="margin-bottom: 8px;">
            <span>查询条件:</span>
            <a-select style="width: 150px;margin:0 50px 0 16px;" v-model='selectType' @change="$router.push('/holdQuery')">
              <a-select-option value="1">查持仓情况</a-select-option>
              <a-select-option value="2">查十大股东</a-select-option>
            </a-select>
            <span>关键字:</span>
            <a-input v-model="content" style="width: 195px;margin: 0 50px 0 30px;" placeholder="股东名称"></a-input>
            <a-button style="margin-right: 20px;" @click="handleReset">重置</a-button>
            <a-button type='primary' @click="getData">查询</a-button>
          </div>
          <div class="table-title">查询结果({{pagination.total}})</div>
        </div>
        <a-table
          :columns="columns"
          :rowKey="record => record.idx"
          :dataSource="tableData"
          :pagination="pagination"
          :loading="loading"
          @change="handleTableChange"
        >
        </a-table>
      </div>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import {
  getShHistoryData
  // getShareHolding // 持股查询
} from '@/api/index'
const columns = [
  { align: 'center', title: '日期', dataIndex: 'date' },
  { align: 'center', title: '当日资金流入', dataIndex: 'capitalInflows' },
  { align: 'center', title: '当日余额', dataIndex: 'balance' },
  { align: 'center', title: '历史资金累计流入', dataIndex: 'totalCapitalInflows' },
  { align: 'center', title: '当日成交静买额', dataIndex: 'netPurchase' },
  { align: 'center', title: '买入成交额', dataIndex: 'buyVolume' },
  { align: 'center', title: '卖出成交额', dataIndex: 'sellVolume' },
  { align: 'center', title: '领涨股', dataIndex: 'name' },
  { align: 'center', title: '领涨股涨跌幅', dataIndex: 'chgOfLedRising' },
  { align: 'center', title: '上证指数', dataIndex: 'indexs' },
  { align: 'center', title: '涨跌幅', dataIndex: 'chgOfIndex' }
]
export default {
  name: 'holderQuery',
  components: {
    Header,
    Footer
  },
  data() {
    return {
      content: '',
      selectType: '2', // 1.持仓 2.股东
      tableData: [],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleReset() {
      this.content = ''
      this.getData()
    },
    handleTableChange(pagination, filters, sorter) {
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        selectType: 2, // 1-持仓情况，2-十大股东
        content: this.content,
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.loading = true
      getShHistoryData(data).then(res => {
        console.log('十大股东')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    }
  }
}
</script>
